<template>
  <div class="citySelect">
    <div class="selectBox">
      <el-select
        ref="city1"
        v-model="province_id"
        focus
        filterable
        placeholder="省份"
        class="text item"
        @change="getCity"
      >
        <el-option
          v-for="item in province"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        >
          {{ item.name }}
        </el-option>
      </el-select>
    </div>
    <div class="selectBox">
      <el-select
        ref="city2"
        v-model="city_id"
        filterable
        placeholder="城市"
        class="text item"
        @change="setData"
      >
        <el-option
          v-for="item in city"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        >
          {{ item.name }}
        </el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
import { log } from "util";
export default {
  name: "shopSearchCity",
  props: {
    title: {
      type: String,
    },
  },
  data() {
    return {
      province_id: null,
      city_id: null,
      province: [],
      city: [],
      defaultData: {
        id: "00000",
        name: "",
      },
    };
  },
  created() {
    //this.getCity(this.defaultData, 0);
  },
  methods: {
    //清空结果
    resetData() {
      this.province_id = null;
      this.city_id = null;
    },
    // 获取一级城市列表
    getProvince(p, c) {
      //填充省份数据
      this.$store.dispatch("shop/getCitySelectList", "00000").then((res) => {
        this.province = res.data;
        if (p && c) {
          this.province_id = p;
          this.city_id = c;
          this.getCity(p, c);
        }
      });
    },
    getCity(p, c) {
      this.$store.dispatch("shop/getCitySelectList", p).then((res) => {
        this.city = res.data;
        if (c) {
          this.city_id = c;
        } else {
          this.city_id = null;
        }
      });
    },
    setData() {
      let result = {
        province_id: this.province_id,
        city_id: this.city_id,
      };
      this.$emit("setCityAreaName", result);
    },
  },
};
</script>

<style lang="scss" scoped>
.citySelect {
  display: flex;
}
.selectBox {
  margin-right: 10px;
}
</style>
